<script lang="ts" context="module">
    let finish: Function;
    export const finished = new Promise((resolve, reject) => {
        finish = resolve;
    });
</script>

<script lang="ts">
    import anime from "animejs";
    import { onMount } from "svelte";
    import parseAPNG, { APNG } from "apng-js";
    import type Player from "apng-js/types/library/player";
    import YTPlayer from "yt-player";
    import { fade } from "svelte/transition";

    function delay(ms: number) {
        return new Promise((resolve) => setTimeout(resolve, ms));
    }

    let eaten = 0;
    async function nom()
    {
        eaten = (eaten + 1) % 4;
        cakeimg.style.marginLeft = `${eaten * 32}px`;
        sfx = "*nom*";
        await delay(500);
        sfx = "\xa0";
    }

    async function loadSprite(
        context: CanvasRenderingContext2D
    ): Promise<Map<string, Player>> {
        const pictures = [
            "walking", "falling", "crawling", "confused", "sitting", "sleeping",
            "thinking", "scared", "collide", "excited", "yawning", "slight-smile"
        ];
        const sprites = new Map<string, Player>();
        return Promise.all(
            pictures.map((img) =>
                fetch(`./sprite/${img}.png`)
                    .then((p) => p.arrayBuffer())
                    .then((buffer) => parseAPNG(buffer))
                    .then(async (apng) => {
                        const player = await (apng as APNG).getPlayer(context);
                        sprites.set(img, player);
                    })
            )
        ).then(() => sprites);
    }

    let text = "\xa0", sfx = "\xa0";
    let character: HTMLElement;
    let cakeimg: HTMLElement;
    let cake_visible: boolean = false;
    let shimeji: HTMLDivElement;
    let mirrored: boolean;
    let hidden: boolean = true;
    let player: Player;

    onMount(async () => {
        const context = (
            shimeji.firstElementChild as HTMLCanvasElement
        ).getContext("2d");
        const sprites = await loadSprite(context);
        player = sprites.get("falling");
        player.stop();
        let complete = new Promise<void>((resolve) => { player.once("end", resolve); });
        hidden = false;
        text = "AAAAAAAAAAAAAAA";

        await anime({
            targets: character,
            translateY: ['-100vh', '0vh'],
            duration: 3000,
            easing: "linear",
        }).finished;
        text = "*bẹp*";
        player.play(); await complete;
        text = "\xa0";

        player = sprites.get("walking");
        player.stop(); await delay(1000);
        text = "Ủa"; await delay(1000);

        player = sprites.get("confused");
        player.stop();
        text = "Tui là ai?"; await delay(500);
        mirrored = true;
        text = "Đây là đâu?"; await delay(2000);

        player = sprites.get("thinking");
        player.stop();
        text = "Được rồi...bình tĩnh nào"; await delay(2000);
        text = "Mình ở đây làm gì ta..."; await delay(1000);
        text = "\xa0"; await delay(1000);
        text = "*nhìn ngày*"; await delay(1000);

        player = sprites.get("scared");
        player.stop();
        text = "Cái đ*t"; await delay(1000);
        text = "Hôm nay là"; await delay(1000);
        text = "11/11!!!!!"; await delay(1000);

        player = sprites.get("walking");
        player.playbackRate = 3.0;
        player.play();
        text = "*panik*";
        for (let elem of [
                {translateX: '25vh'}, 
                {translateX: '-25vh'},
                {translateX: '50vh'},
                {translateX: '-50vh'},
                {translateX: '75vh'},
                {translateX: '-75vh'},
                {translateX: '100vh'}
            ]
        )
        {
            await anime({
                targets: character,
                ...elem,
                duration: 1000,
                easing: "linear"
            }).finished;
            mirrored = !mirrored;
        }
        player.stop();
        player.playbackRate = 1.0;
        mirrored = true;

        player = sprites.get("collide");
        player.stop();
        character.style.marginLeft = `-${175+64}px`;
        text = "*bẹp*"; await delay(2000);
        player.renderNextFrame();
        text = ":maika:"; await delay(2000);

        player = sprites.get("crawling");
        character.style.marginLeft = "-175px";
        mirrored = false;
        player.play();
        text = "aaaa...đau quá";
        delay(1000).then(() => {text = "(chỗ này fail vcl, mà thôi kệ)"})
                   .then(() => delay(1000))
                   .then(() => {text = "\xa0"});
        await anime({
            targets: character,
            translateX: "0vh",
            duration: 5000,
            easing: "linear"
        }).finished;
        player.stop();

        player = sprites.get("walking");
        player.stop();
        text = "Ờm thì..."; await delay(1000);
        text = "Bây giờ mua quà thì cũng khá khó khăn"; await delay(2000);
        text = "Nên là..."; await delay(1000);

        player = sprites.get("excited");
        player.stop();
        cake_visible = true;        
        text = "Ăn bánh khum :3"; await delay(2000);
        text = "(click thử đi, ăn được á)"; await delay(2000);

        text = "Oke, giờ thì nghe nhạc nè";
        const ytPlayer = new YTPlayer("#ytplayer", {
            width: 640, height: 480
        });
        ytPlayer.load("n-u--3EhX7Q");
        const videoplaying = new Promise<void>((resolve) => ytPlayer.on("playing", 
            () => { resolve() }));
        const videofinish = new Promise<void>((resolve) => ytPlayer.on("ended", 
            () => { resolve() }));
        let prevTimepoint = undefined;
        ytPlayer.on("timeupdate", (seconds) => {
            let timepoint: number;
            if (ytPlayer.getState() === "playing")
            {
                if (seconds >= 4 * 60)
                    timepoint = 2;
                else if (seconds >= 2 * 60)
                    timepoint = 1;
                else
                    timepoint = 0;
            }
            if (timepoint !== prevTimepoint)
            {
                prevTimepoint = timepoint;
                player.stop();
                switch (timepoint)
                {
                    case 0:
                        player = sprites.get("walking");
                        player.stop(); break;
                    case 1:
                        player = sprites.get("sitting");
                        player.play(); break;
                    case 2:
                        player = sprites.get("sleeping");
                        player.play(); break;
                }
            }
        })
        await videoplaying;

        player = sprites.get("walking");
        player.play();
        mirrored = true;
        text = "\xa0";
        await anime({
            targets: character,
            translateX: "75vh",
            duration: 5000,
            easing: "linear",
        }).finished;
        player.stop();
        mirrored = false;
        await delay(1000); text = "Nhớ xem hết nha";
        await delay(1000); text = "(hay tua cũm đc)";
        await delay(1000); text = "\xa0";
        await videofinish; player.stop();
        
        await delay(2000);
        player = sprites.get("yawning");
        player.stop();
        text = "*ngáppppp*"; await delay(1500);
        text = "\xa0";

        player = sprites.get("walking");
        player.play();
        await anime({
            targets: character,
            translateX: "0vh",
            duration: 5000,
            easing: "linear",
        }).finished;
        player.stop();
        await delay(1000);

        text = "Trước tiên thì..."; await delay(1000);
        text = "Anh xin lỗi vì ko có một món quà như những người khác"; await delay(3000);
        text = "Nhưng mà..."; await delay(1000);
        player = sprites.get("slight-smile"); player.stop();
        text = "Mong là cái này có thể giúp em vui hơn :3"; await delay(2500);
        player = sprites.get("excited"); player.stop();
        text = "Iu em ❤"; await delay(1000);
        text = "Moaz 😘"; await delay(1500);
        player = sprites.get("walking");
        player.play();
        text = "\xa0";
        await anime({
            targets: character,
            translateX: "-125vh",
            duration: 5000,
            easing: "linear",
        }).finished;
        finish();
    });
</script>

<article id="stage">
    <div id="character" class="sprite-with-text" style="left: 50%; margin-left: -170px;" bind:this={character} {hidden}>
        <span id="dialogue" class="text" style="width: 350px; overflow-wrap: break-word">{text}</span>
        <div
            id="shimeji"
            class={mirrored ? "mirrored" : ""}
            bind:this={shimeji}
            width="128px"
            height="128px"
        >
            <canvas />
        </div>
    </div>
    {#if cake_visible}
    <div id="cake" class="sprite-with-text" style="left: 40%; margin-left: -32px" transition:fade>
        <span class="text" style="width: fit-content;">{sfx}</span>
        <img src="./sprite/cake_side.png" style="image-rendering: pixelated;" width="64px" height="64px" alt="a cake" bind:this={cakeimg} on:click="{nom}"/>
    </div>
    {/if}
    <div id="ytplayer" />
</article>

<style>
    #stage {
        width: 100vw;
        height: 100vh;
        background-color: black;
        left: 0;
        top: 0;
        overflow: hidden;
    }
    .sprite-with-text {
        display: flex;
        position: absolute;
        bottom: 0px;
        flex-direction: column;
        align-items: center;
    }
    #cake {
        left: 50vh;
        margin-left: -32px;
        overflow: hidden;
        width: 64px;
        display: flex;
    }
    #shimeji {
        overflow: hidden;
        width: 128px;
        height: 128px;
    }
    #ytplayer
    {
        position: absolute;
        transform: translate(-50%, -50%);
        left: 50%; top: 50%
    }
    .text {
        font-size: 1.5em;
        color: white;
        margin: 0;
        text-align: center;
    }
    .mirrored {
        transform: scale(-1, 1);
    }
</style>
